<template>
    <div class="list-test">
        <u-list width="100px" height="500px" @on-item-selected="handleItemSelected" @on-item-hovered="handleItemHovered" :data="testData" item-size="100px 60px" :option="option1" direction="column"></u-list>

        <div class="list-test-row">
            <u-list width="500px" height="100px" @on-item-selected="handleItemSelected" :data="testData" item-size="100px 100px" :option="option2">
                <template slot-scope="{ index }" slot="row">
                    <u-bitmap width="100px" height="100px" :skin="webpImg"><strong>{{index}}</strong></u-bitmap>
                </template>
            </u-list>

            <u-list width="500px" height="100px" @on-item-selected="handleItemSelected" :data="testData" item-size="100px 100px" :option="option2">
                <template slot-scope="{ item }" slot="row">
                    <u-bitmap width="100px" height="100px" :skin="webpImg">
                        <u-button width="50px" height="50px" :skin="img12" rule="1,3||20,10,20,10">{{item}}</u-button>
                    </u-bitmap>
                </template>
            </u-list>
        </div>
    </div>
</template>
<script>
    import webpImg from './img/loading.webp';
    import svgaSrc2 from './img/svga/rich_bg_lv2.svga';
    // import publishSrc from './img/svga/blinddate_publish.svga';

    import img12 from './img/carddating/btn_on.png';

    export default {
        data () {
            return {
                webpImg,
                svgaSrc2,
                img12,
                list: ['0000000', '00000000000000', '000000000000000000'],
                option1: {
                    render: (h, params) => {
                        return h('u-bitmap', {
                            props: {
                                skin: webpImg,
                                width: '60px',
                                height: '60px'
                            }
                        }, [
                            h('strong', params.index)
                        ]);
                    }
                },
                option3: {
                    render: (h, params) => {
                        return h('u-button', {
                            props: {
                                skin: img12,
                                rule: '1,3||20,10,20,10',
                                width: '80px',
                                height: '80px'
                            }
                        }, [
                            h('strong', params.index)
                        ]);
                    }
                },
                option2: {
                    slot: 'row'
                },
            };
        },
        computed: {
            testData () {
                const result = [];
                for (var i = 0; i <100000; ++i) {
                    result.push(`test ${i}`);
                }
                return result;
            }
        },
        methods: {
            handleItemSelected (index) {
                console.info('Selected==========================', index);
            },
            handleItemHovered (index) {
                console.info('Hovered***************************', index);
            }
        }
    };
</script>
<style>
    .list-test {
        display: flex;
        height: 800px;
        overflow: hidden;
        overflow-y: auto;
    }
    .list-test-row {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
</style>




